.form_heading {
  font-weight: lighter;
  font-size: 24px;
  line-height: normal;
  text-align: center;
  color: #ffffff;
  padding: 21px 44px 50px;
  margin: 0;
}

.contest_description {
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  color: #283645;
  padding: 45px 20px 35px 20px;
}

.contest_description_invitation {
  display: none;
}

.form_field {
  display: flex;
  flex-direction: column;
  padding-bottom: 23px;
}

.form_text-label {
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  color: #d22856;
  padding-left: 13%;
  margin-bottom: 6px;
}

.form_text-input {
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  color: #283645;
  width: 80%;
  margin: 0 auto;
  height: 58px;
  padding-left: 22px;
  border: 2px #f2f2f2 solid;
}

.form_text-input:hover {
  border: 2px #283645 solid;
}

.form_text-input:active {
  border: 2px #d22856 solid;
}

.form_field-text--fullname {
  margin-bottom: 16px;
}

.form_field-text {
  display: flex;
  flex-direction: column;
}

.form_fieldset {
  margin: 0;
  padding: 0;
}

.form_fieldset--achievements {
  background-color: #f2f2f2;
  border: none;
}

.form_title {
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  color: #d22856;
  text-transform: uppercase;
  margin: 0;
  text-align: center;
  padding: 40px 22px;
}

.form_title--achievements {
  width: 300px;
  margin: 0 auto;
  line-height: 18px;
  background-color: #f2f2f2;
  text-align: center;
}

.form_checkbox-label {
  display: block;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  color: #283645;
  position: relative;
  margin-left: 25%;
  padding: 26px 0;
}

.form_checkbox-input {
  display: none;
}

.form_checkbox-input ~ .form_checkbox-label::before {
  content: "";
  position: absolute;
  border: 2px #e5e5e5 solid;
  background-color: #ffffff;
  width: 34px;
  height: 34px;
  top: 18px;
  left: -62px;
}

.form_checkbox-input ~ .form_checkbox-label:hover::before {
  border: 2px #283645 solid;
}

.form_checkbox-input ~ .form_checkbox-label:active::before {
  border: 2px #d22856 solid;
}

.form_checkbox-input:checked ~ .form_checkbox-label::before {
  content: "";
  background-image: url("../img/icon-yes.svg");
  background-repeat: no-repeat;
  background-size: 23px;
  background-position: center;
  position: absolute;
  border: 2px #e5e5e5 solid;
  background-color: #ffffff;
  width: 34px;
  height: 34px;
  top: 18px;
  left: -62px;
}

.form_checkbox-input:checked ~ .form_checkbox-label:hover::before {
  border: 2px #283645 solid;
}

.form_checkbox-input:checked ~ .form_checkbox-label:active::before {
  border: 2px #d22856 solid;
}

.form_fieldset--contacts {
  padding-top: 41px;
  border: none;
}

.form_text-input--contacts {
  margin: 0 5%;
}

.form_fieldset--apps {
  background-color: #f2f2f2;
  border: none;
}

.form_title--apps {
  padding: 38px 13% 20px;
  line-height: 18px;
  background-color: #f2f2f2;
}

.form_fieldset--emotions {
  border: none;
}

.form_radio-input {
  display: none;
}

.form_radio-label {
  font-size: 18px;
  line-height: 30px;
  display: block;
  position: relative;
  margin-left: 28%;
  padding: 22px 0;
}

.form_radio-input ~ .form_radio-label::before {
  content: "";
  position: absolute;
  border: 2px #e5e5e5 solid;
  border-radius: 50%;
  background-color: #ffffff;
  width: 38px;
  height: 38px;
  top: 15px;
  left: -71px;
}

.form_radio-input:checked ~ .form_radio-label::before {
  content: "";
  background-image: url("../img/icon-map-marker.svg");
  background-repeat: no-repeat;
  background-size: 40px;
  background-position: center;
  position: absolute;
  border: 2px #e5e5e5 solid;
  border-radius: 50%;
  background-color: #ffffff;
  width: 38px;
  height: 38px;
  top: 15px;
  left: -71px;
}

.form_radio-input ~ .form_radio-label:hover::before {
  border: 2px #283645 solid;
}

.form_radio-input ~ .form_radio-label:active::before {
  border: 2px #d22856 solid;
}


.form_field-text--contacts {
  margin-bottom: 17px;
}

.form_fieldset-inner {
  width: 100%;
  padding-bottom: 20px;
}

.form_footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  position: relative;
}

.form_error {
  margin: 0 auto;
  width: 320px;
  height: 290px;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
  display: none;
}

.form_error_wrapper {
  width: 280px;
  margin: 0 auto;
}

.form_error_description {
  margin: 0;
  text-align: center;
  font-size: 18px;
  line-height: 24px;
  padding-top: 45px;
}

.form_error_text {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  text-align: center;
  padding-top: 35px;
}

.form_sucsess {
  margin: 0 auto;
  width: 320px;
  height: 380px;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
  display: none;
}

.form_sucsess_wrapper {
  width: 280px;
  margin: 0 auto;
}

.form_sucsess_description {
  margin: 0;
  text-align: center;
  font-size: 18px;
  line-height: 24px;
  padding-top: 45px;
}

.form_sucsess_text {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  text-align: center;
  padding: 0 7px;
  padding-top: 35px;
  padding-bottom: 20px;
}

.form_submit-button {
  border: 0;
  width: 280px;
  margin: 0 auto;
  padding: 17px 0;
  background-color: #2aab6d;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 30px;
  color: #ffffff;
  margin-bottom: 35px;
}

.form_submit-button:active {
  color: #61b98f;
}

.form_submit-error {
  margin-bottom: 0;
  margin-top: 40px;
}

.form_submit-sucsess {
  width: 280px;
  margin-left: 20px;
  margin-bottom: 0;
  margin-top: 55px;
}

.form_submit-block {
  width: 320px;
  height: 163px;
  margin: 0 auto;
  background-color: #f2f2f2;
}

.form_note {
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  color: #283645;
  text-transform: uppercase;
}

.form_note-symbol {
  color: #d22856;
  text-transform: uppercase;
}

.form_title--contacts {
  display: none;
}

.form_title--emotions {
  padding: 30px 22px 10px;
}

.form_textarea--emotions {
  font-family: "Open Sans", "Arial", sans-serif;
  color: #283645;
  display: flex;
  width: 275px;
  height: 182px;
  text-align: center;
  padding-top: 20px;
  margin: 0 auto;
  margin-bottom: 28px;
  font-size: 18px;
  line-height: 30px;
  resize: none;
}

.form_note-symbol--desktop {
  display: none;
}

